import Component from 'vue-class-component'

@Component({
  name: 'Search',
  props: {
    input: {
      type: Function,
      default: (): void => {}
    }
  }
})
export default class Search extends Vue {
  text: any = ''
  searchStates: boolean = false
  focus() {
    this.searchStates = true
  }
  blur() {
    this.searchStates = false
  }
  clearText() {
    this.text = ''
  }
  search() {
    this.$emit('input', this.text)
  }
}



